      // console.log(this.$store.state.arr)
      // console.log(this.$store.getters.fetArr)
       
  },
  computed:{
      // ...mapState([
      //       'name'
      //   ]),
      //   ...mapGetters([
      //     'fetArr'
      //   ])
  },
  beforeRouteEnter:((to,from,next)=>{
    console.log('你要进入组件路由')
    next()
  }),
  beforeRouteLeave:((to,from,next)=>{
    console.log(to)
  }),
  beforeRouteUpdate:((to,from,next)=>{
    console.log(to)
  }),
  //测试双向数据绑定原理
 
}
</script>


